import React from 'react';
import './App.scss';
import Names from './names.json';
//import { DaysInMonth, GengoYear, getJulianDay, getMoonAgeJD } from './util-cal';
import { DaysInMonth, GengoYear } from './util-cal';

const locale = "jp";  // en, jp, kr

const Month = ({yr, m, name, bFill=true}) => {
  const N = DaysInMonth(m, yr);
  const dow = (new Date(yr, m-1, 1)).getDay();  // Sun:0 ~ Sat:6
  const lns = [];
  Names.Week[locale].forEach( (w, i) => lns.push( <li key={i}>{w}</li> ));
  const prevN = (m === 1) ? 31 : DaysInMonth(m-1, yr);
  for (let j=1; j <= dow; j++)  // previous month
    lns.push( <li key={10+j} className="m_pv">{bFill ? (prevN - dow + j) : ''}</li> );

  //let jd = getJulianDay(new Date(yr, m-1, 1, 21));
  for (let d=1; d <= N; d++) {  // this month
    //const ma = getMoonAgeJD(jd + d - 1);
    lns.push( <li key={20+d}>{d}</li> );
  }
  if (bFill) {
    for (let k=1; k <= (7*6 - dow - N); k++)  // next month
      lns.push( <li key={60+k} className="m_nx">{k}</li> );
  }

  return (
    <section  className="mon">
      <header className="mon_h">{name}</header>
      <ol     className="mon_b">{lns}</ol>
      <footer className="mon_f"><span role="img" aria-label="moons">ðŸŒ• ðŸŒ— ðŸŒ‘ ðŸŒ“</span></footer>
    </section>
  );
}

function App() {
  const yr=(new Date()).getFullYear();  // Check 1752
  const lns = [];
  Names.Month[locale].forEach( (nm, i) => lns.push( <Month key={i} yr={yr} m={i+1} name={nm} bFill={true} />))
  return (
    <div      className="App">
      <header className="cal_h">{yr} {GengoYear(yr)}å¹´ {window.innerWidth}x{window.innerHeight}</header>
      <main   className="cal_b">
        {lns}
      </main>
    </div>
  );
}

export default App;
